@import "../../styles/fonts";

.hidden {
  display: none;
}

.block {
  display: block !important;
}
// * 1.  
.topbar_container {
  z-index: 100;
  // position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  position: fixed; //  position: -webkit-sticky
  // position: -webkit-sticky;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #f8f8f8;
  color: #000000;
  font-family: "Pretendard-Medium";
  box-sizing: border-box;
  padding: 0;
  height: 6.25rem; //10px
  // column-gap: 30px;

  .navbar_conainer {
    // width: 100%;
    // height: 100%;
    // // background-color: #ff5353;
    // max-width: var(--desktop-width);  //   
    // display: flex;
    // box-sizing: border-box;
    // flex-direction: row;
    // align-items: center;
    // // justify-content: start;
    justify-content: space-between; //   
    width: 100%;
    height: 100%;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: start;
    column-gap: 145px; //   ;
    max-width: 1240px;

    box-sizing: border-box;
    flex-direction: row;

    // // * ,   (  )
    // .logo_nav_container{
    //     width: 100%;
    //     height: 100%;
    //     flex: 1;
    //     display: flex;
    //     align-items: center;
    //     justify-content: start;
    //     column-gap: 145px; //   ;

    //     box-sizing: border-box;
    //     flex-direction: row;

    // *   
    .logo_container {
      display: flex;
      height: 100%;
      justify-content: center;
      align-items: center;
      // *  
      .logo_img {
        height: 90px; // 64px
        cursor: pointer;
      }
    }

    // * 2.   
    .main_container {
      display: flex;
      flex: 1;
      box-sizing: border-box;
      min-width: 50%;

      height: 100%;

      //  ul 
      .menu_all_container {
        margin: 0px;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        column-gap: 60px;
        align-items: center;
        justify-content: start; //   
        list-style-type: none; //  
        position: relative;

        //  , ,  
        .menus_container {
          height: 100%;

          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;

          // 
          .menu_title {
            font-family: "Pretendard-Bold";
            cursor: pointer;
            white-space: nowrap;
            font-size: 21px;
            position: relative;
            // padding: 8px 0;

            > a {
              color: inherit; //   
              text-decoration: none; //  
            }
          }

          &:hover {
            color: #4095a2;
          }
          // * hover  
          &:hover > .menu_title::after {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            bottom: -7px; /*    */
            height: 3px; /*    */
            background-color: #4095a2; /*   */
            border-radius: 3px;
          }

          // menu_title hover  sub_menus  
          &:hover > .sub_menus {
            display: flex;
            justify-content: center;
            flex-direction: row;
            align-items: stretch;
            column-gap: 202px;
          }

          // *  
          .sub_menus::before {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw; /*     */
            right: 0; /*     */
            width: 200vw; /*     left, right  */
            height: 100%;
            background-color: #ffffff;
            // padding: 40px; //40px
            // min-width: 200px;

            // box-sizing: border-box;

            border: 1px solid #d0d0d0;
            z-index: -1;

            // padding: 40px; //40px
          }

          // *    (, , ,  ... )
          .sub_menus {
            display: none;
            position: absolute;

            top: 100px;
            left: 0px;

            // background-color: #FFFFFF;
            list-style: none;

            min-width: 200px;

            box-sizing: border-box;
            padding: 40px; //40px
            // border: 1px solid #D0D0D0;
            // // *  

            // *    (, , ,  ... )
            .sub_sub_menus {
              display: flex;
              flex-direction: column;
              padding-left: 20px;
              text-align: left;

              position: relative;
              left: -50%;

              position: relative;
              // *   
              &::before {
                content: "";
                position: absolute;
                height: 100%;
                // height: calc(100% - 8px); // 100% + 7px 
                max-width: 2px;
                width: 2px;
                top: 0px;
                left: 0px;

                background-color: #eaeaea;
                border-radius: 2px;
              }
              //  
              .sub_menu_title {
                height: 24px;
                font-size: 20px;
                font-family: "Pretendard-SemiBold";
                color: #000000;
                text-decoration: none; //  
                font-weight: bold;
                margin: 0 0;
                margin-bottom: 35px; //35px
                white-space: nowrap;
                // margin: 0.5rem 0px;

                // &:hover{
                //     color: #4095A2;
                // }
              }

              // * 
              .sub_menu_items {
                white-space: nowrap; //  .
                display: flex;
                flex-direction: column;
                row-gap: 20px;
                margin: 0;
                padding: 0;
                list-style: none;
                text-align: left;
                justify-content: center;
                font-size: 19px;
                font-family: "Pretendard-SemiBold";

                // *    
                .sub_menu_item {
                  height: 23px;
                  display: flex;
                  flex-direction: column;
                  text-align: left;
                  justify-content: center;
                  margin-bottom: 0 0;

                  > a {
                    text-decoration: none; //  
                    color: #767676;

                    &:hover {
                      color: #4095a2;
                    }
                  }

                  &:last-child {
                    margin-bottom: 0;
                  }

                  cursor: pointer;
                }
              }
            }
          }
        }
        // }
      }
    }

    // * 3.    
    .add_menu_container {
      //   margin-right: 84.73px;
      // flex: 1;
      .menu_img {
        // display: none;
        // margin-left: auto;
        // padding-left: auto;
        width: 1.5rem;
        height: 1.5rem;
        cursor: pointer;
      }
    }
  }
}

/*  ,   ( 480px ~ 767px)*/

//    (  576px    )
@media screen and (max-width: 1023px) {
  //*   
  .topbar_container {
    // min-width: 360px;
    // background-color: #95cdd5;
    height: 4.0625rem; // 65px
    width: 100vw;

    //*       
    .navbar_conainer {
      // flex-direction: column;
      column-gap: 0px;
      // *    
      .logo_container {
        position: relative;
        left: 25px;
        height: 4.0625rem; // 65px

        .logo_img {
          width: 101px; //180px
          height: 65px; // 64px // *    ?
        }
      }

      // * 2.   ( )
      .main_container {
        min-width: none;
        align-items: start;
        // background-color: #F8F8F8;

        .menu_all_container {
          position: fixed;
          display: none; // *  .     
          top: 65px;
          left: 0;
          width: 150px;
          height: 100%;

          background-color: #f8f8f8;
          border-top: 1px solid #d0d0d0;
          border-right: 1px solid #d0d0d0;
          // display: ;
          // *   

          // *   ( ,  ,  ,  , 
          flex-direction: column;
          align-items: start;
          text-align: start;

          //  , ,  
          .menus_container {
            width: 150px;
            height: 44px;
            color: #000000;

            // position: relative;
            //  ( ,  ,  ,  , 
            .menu_title {
              width: 100%;
              font-family: "Pretendard-SemiBold";
              text-align: start;
              cursor: pointer;
              font-size: 16px;
              box-sizing: border-box;
              padding-left: 25px;

              // color: #000000;
              // padding: 8px 0;
            }

            &:hover {
              background-color: #4095a2;
              color: #ffffff !important;
            }

            &:hover > .menu_title::after {
              content: url(./arrow.svg); // > 
              position: relative;
              top: 1px;
              bottom: 0px;
              height: auto;
              left: 10px;
              background-color: none; /*   */
              border-radius: 0px;
            }

            // *menu_title hover  sub_menus  
            &:hover > .sub_menus {
              flex-direction: column;
              row-gap: 35px;
            }

            // *  
            .sub_menus::before {
              content: "";
              position: fixed;
              top: 65px;
              left: 150px; /*     */
              right: 0; /*     */
              width: calc(
                100% - 150px
              ); /*     left, right  */
              // width: 300px;
              height: 100%;
              background-color: #ffffff;
              // padding: 40px; //40px
              // min-width: 200px;

              // box-sizing: border-box;
              // border-left: 1px solid #D0D0D0;
              border-left: 0px;
              border-right: 1px solid #d0d0d0;
              border-bottom: 1px solid #d0d0d0;
              border-top: 1px solid #d0d0d0;
              z-index: -1;

              // padding: 40px; //40px
            }
            // *    (, , ,  ... )
            .sub_menus {
              position: fixed;
              top: 65px;
              left: 150px;
              min-width: 210px;
              padding-left: 20px;
              padding-right: 25px;
              padding-top: 12px;
              padding-bottom: auto;

              // *    (, , ,  ... )
              .sub_sub_menus {
                bottom: 0px;
                left: 0px;
                padding-left: 0px;
                // *   
                &::before {
                  display: none;
                }

                //  
                .sub_menu_title {
                  height: 18px;
                  font-size: 15px;
                  font-family: "Pretendard-SemiBold";
                  color: #353535;
                  text-decoration: none; //  
                  font-weight: normal;
                  margin: 0 0;
                  margin-bottom: 15px; //35px
                  // margin: 0.5rem 0px;

                  // &:hover{
                  //     color: #4095A2;
                  // }
                }

                // * 
                .sub_menu_items {
                  row-gap: 12px;
                  font-size: 14px;

                  // *    
                  .sub_menu_item {
                    height: 17px;
                  }
                }
              }
            }
          }
        }
      }
      // * 3.    
      .add_menu_container {
        // flex: 1;

        padding-right: 25px;

        .menu_img {
          display: block; // .
          cursor: pointer;
        }
      }
    }
  }
}
