@import '../../styles/fonts';
@import '../../styles/variables';



//*  
.container{
    min-width: 11.875rem; //190px
    height: 100%;
    // background-color: antiquewhite;

    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    font-family: 'Pretendard-Medium';

    .nav_container{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        row-gap: 10px;


        //Skdebar  
        .title_sidebar{
            background-color: #4095A2;
            border-radius: 10px;
            width: 100%;
            min-height: 7.5625rem;
            
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            
            font-size: 1.125rem; // 18px
            text-align: center;
            color: #FFFFFF;
            font-family: 'Pretendard-SemiBold';
        }

        //    
        .menu_container{
            width: 100%;
            display: flex;
            flex-direction: column;
            list-style-type: none; //  

            padding: 0;
            margin: 0;
            row-gap: 10px;

            //     1 
            .subMenu_container{
                display: flex;
                flex-direction: column;
                list-style-type: none; //  
                
                
                //   (ex. 1, 2,, )
                .subMenu_title{
                    height: 2.4375rem; //39px
                    border-radius: 5px;
                    display:flex;
                    align-items: center;
                    justify-content: left;

                    box-sizing: border-box;
                    padding: 10px;

                    background-color: #E9F4F6;

                    font-size: 0.9375rem;
                    font-family: 'Pretendard-SemiBold';
                    

                    // &:hover {
                    //      cursor: pointer;
                    //     color: #4095A2;
                    //     border-radius: 5px;
                    //     border: 1.5px solid #4095A2;
                    // }

                    .item_text{ //   
                        flex: 1;
                    }

                    .item_img{ //  ( none)
                        display: none; //  
                    }


                }



                //    
                .subMenu_item_container{
                    display: flex;
                    flex-direction: column;
                    list-style-type: none; //  
                    padding: 0;
                    //  
                    .subMenu_item{
                        cursor: pointer;
                        display:flex;
                        align-items: center;
                        justify-content: left;
                        font-size: 0.9375rem; //15px

                        list-style-type: none; //  
                        height: 2.4375rem; //39px
                        // border-radius: 5px;

                        box-sizing: border-box;
                        padding: 10px;

                        background-color: #FFFFFF;
                        border-bottom: 1px solid #D0D0D0;

                        &:hover {
                            color: #4095A2;
                            border-radius: 5px;
                            border: 1.5px solid #4095A2;
                        }

                        .item_text{
                            flex: 1;
                        }

                        .item_img{ //  ( none)
                            display: none;
                        }


                        
                    }
                }



            
            }
        }

    }
}


// *  
.clicked {
    font-weight: bold; // :  
    color: #4095A2;
    border: 1.5px solid #4095A2 !important; //border    (css  )
    border-radius: 5px; 
    
}

.clicked_img{
    display: inline-block;
    // width: 15px;
    height: 13px;
    
}

.page {
    display: flex;
    gap: 105px;
    max-width: var(--desktop-width);
    padding-top: 64px;

    .page_content{
        display: flex;
        flex-direction: column;
        width: 100%;

        .nav {
            padding: 35.41px 0 0 0;
            display: flex;
            flex-direction: row;
            font-family: "Pretendard-Medium";
            font-size: 14px;
            color: #707070;
            align-items: center;

            li {
                &::after{
                    content: '>';
                    padding: 0 15px;
                }
                &:last-child::after,
                &:first-child::after{
                    content: '';
                    padding: 0 10px 0 0;
                }
                &:last-child{
                    color: #000;

                }
            }
        }
        h1{
            font-family: "Pretendard-Bold";
            font-size: 22px;
            padding: 40px 0 30px 0;
        }
    }
}


// *   ( 1023px )
@media screen and (max-width: 1023px) {
	// aside {
    .container{
        // visibility: hidden;
		display: none;
	}
    .page {
        width: 100%;
        justify-content: center;
        padding-top: 0;
        .page_content{
            max-width: 360px;
            .nav{
                font-size: 11px;
                li {
                    &::after{
                        content: '>';
                        padding: 0 13px;
                    }
                }
            }
            .page_header{
                padding: 40px 0px 20px 0;
                font-size: 16px;
            }
        }

        
    }
}