@import './fonts';

//  : 052)264-1017
@mixin callout{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-height: 60px;
    border: 1px solid #4095A2;
    color: #307B86;
    font-family: "Pretendard-SemiBold";
    font-size: 18px;
    
    span { //  
        font-family: "Pretendard-Bold"
    }

    // * 
    @media screen and (max-width:1023px) {
        max-width: 310px;
        font-size: 14px;
    }
}   

//  : @include resuable-style;

// > 30
@mixin subheading {
    font-family: "Pretendard-SemiBold";
    color: #353535;
    font-size: 18px;
    margin: 0;
    
    &::before{
        content: " ";
    }


    @media screen and (max-width:1200px) {
        font-size: 15px;
    }

}
// 


// *  X  ()
@mixin tableXscroll {
        //   
    overflow-x: scroll;
    padding-bottom: 15px; //    20px   (   15px  .)

    //   
    &::-webkit-scrollbar{
        
        width: 310px;
        height: 20px;
        color: #D0D0D0;
        border-top: 1px solid #D0D0D0;
        border-bottom: 1px solid #D0D0D0;
    }

    //  thumb() 
    &::-webkit-scrollbar-thumb{
        width: 100px;
        height: 12px;
        background-color: #E0E0E0;
        border-radius: 50px;
        background-clip: padding-box;
        border: 4px solid transparent;
    }

    //  (thumb  ) 
    &::-webkit-scrollbar-track{
        
        width: 200px;
        height: 30px;
        border-radius: 50px;
    }

}